.todoList{
    margin: 4rem auto;
    padding: 2rem 3rem 3rem;
    max-width: 500px;
    background: #FF6666;
    color: #FFF;
    box-shadow: -20px -20px 0px 0px rgba(100, 100, 100, .1);
}

.todoList-h1{
    /*text-align:center;*/
    font-weight: normal;
    font-size: 2.6rem;
    letter-spacing: 0.05em;
}

.todoList-span{
    display: block;
    font-size: 0.8rem;
    margin-bottom: 0.7rem;
    margin-left: 3px;
    margin-top: 0.2rem;
    border-bottom: 1px solid rgba(255, 255, 255, .3);
}

@keyframes strikeitem {
    to {
        width: calc(100% + 1rem);
    }
}